*,*::before,*::after{box-sizing:border-box}body{margin:0;font-family:-apple-system, "Roboto", BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-size:13px;color:#4b4b4b;background-color:#f5f5f5}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}a{cursor:pointer;color:#2680eb;text-decoration:none}a:hover,a:focus{color:#0d66d0}.container{display:flex;flex-direction:column;justify-content:center;margin:1em}@media screen and (min-width: 375px){.container{width:600px;margin-left:auto;margin-right:auto}}

.loading-indicator{display:flex;align-items:center}.loading-indicator::before{content:"";display:inline-block;width:1.26em;height:1.26em;margin-right:1em;border:2px solid;border-color:#1473e6 #e1e1e1;border-radius:50%;-webkit-animation:rolling 1s linear infinite;animation:rolling 1s linear infinite}@-webkit-keyframes rolling{from{transform:rotate(0)}to{transform:rotate(360deg)}}@keyframes rolling{from{transform:rotate(0)}to{transform:rotate(360deg)}}

.grid-table{border-spacing:0}.grid-table tbody{border:1px solid #cacaca;border-bottom:1px none;border-radius:4px;background-color:#fff}.grid-table tbody tr:first-of-type td{border-top:1px solid #cacaca}.grid-table tbody tr:first-of-type td:first-of-type{border-radius:4px 0 0}.grid-table tbody tr:first-of-type td:last-of-type{border-radius:0 4px 0 0}.grid-table tbody tr:last-of-type td:first-of-type{border-radius:0 0 0 4px}.grid-table tbody tr:last-of-type td:last-of-type{border-radius:0 0 4px}.grid-table tbody td{border-bottom:1px solid #cacaca;border-top:1px none}.grid-table tbody td:first-of-type{border-left:1px solid #cacaca}.grid-table tbody td:last-of-type{border-right:1px solid #cacaca}.grid-table th,.grid-table td{text-align:left;padding:.5rem 1rem}.grid-table th{font-weight:normal;background-color:transparent;color:#6e6e6e}@media screen and (min-width: 375px){.grid-table{width:100%}}

.filters{display:flex;justify-content:space-between;margin-bottom:1rem}

.checkbox{display:flex;align-items:center}.checkbox label{-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}

.search{position:relative}.search label[for="name-search"]{position:absolute;top:calc(50% - 1px);left:0;transform:translate3d(0, -50%, 0);line-height:1;color:#8e8e8e}.search .field{padding:.5rem 1rem .5rem 5em;border:1px none;border-bottom:1px solid #1473e6;width:150px;background-color:transparent;outline:none;transition:width .4s ease-in-out}.search .field:focus{width:300px;border-bottom-color:#095aba}


.dl-group{display:flex;padding:1rem 0;border-bottom:1px solid #cacaca}.dl-group dt{min-width:8em}.dl-group dd{margin-left:0}

.nav{display:flex;justify-content:space-between}.nav .go-back{cursor:pointer;color:#2680eb;text-decoration:none;display:flex;align-items:center}.nav .go-back:hover,.nav .go-back:focus{color:#0d66d0}.nav .go-back::before{content:"";display:inline-flex;width:0.5em;height:0.5em;margin-right:0.2em;border:2px solid currentColor;border-style:none none solid solid;transform:rotate(45deg)}

h1{margin:2rem 0;font-size:3rem;letter-spacing:-0.05em;font-family:-apple-system, "Montserrat", BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu"}

.full-screen{display:flex;justify-content:center;align-items:center;min-height:100vh;font-size:3rem;font-weight:900;letter-spacing:-0.05em}

